import Vue from "vue";
import VueRouter from "vue-router";

import Index1 from "./pages/index1.vue";
import Achievement from "./pages/achievement.vue";
import Tool from "./pages/tool.vue";
import StudentList from "./pages/studentList.vue";
import NewList from "./pages/newList.vue";
import NewInfo from "./pages/newInfo.vue";
import Culture from "./pages/culture.vue";
import StudentInfo from "./pages/studentInfo.vue";
import TeacherInfo from "./pages/teacherInfo.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Index-1",
    component: Index1
  },
  {
    path: "/achievement",
    name: "Achievement",
    component: Achievement
  },
  {
    path: "/tool",
    name: "Tool",
    component: Tool
  },
  {
    path: "/culture",
    name: "Culture",
    component: Culture
  },
  {
    path: "/studentList",
    name: "StudentList",
    component: StudentList
  },
  {
    path: "/newList",
    name: "NewList",
    component: NewList
  },
  {
    path: "/newInfo",
    name: "NewInfo",
    component: NewInfo
  },
  {
    path: "/studentInfo",
    name: "StudentInfo",
    component: StudentInfo
  },
  ,
  {
    path: "/teacherInfo",
    name: "TeacherInfo",
    component: TeacherInfo
  },
  {
    path: "/index-1",
    component: Index1
  },
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

export default router;
