<script>
/**
 * About component
 */
import axios from "axios";
import * as csvQueryStudent from '../assets/js/student.js';
import * as csvQueryPaper from '../assets/js/paper.js';
import * as csvQueryPatent from '../assets/js/patent.js';

export default {
    name: 'StudentInfo',
    data() {
        return {
            student: {},
            CCF_info: [
                "其他",
                "T1、A类中文期刊",
                "A类国际期刊",
                "B类国际期刊",
                "C类国际期刊",
                "A类国际会议",
                "B类国际会议",
                "C类国际会议"
            ],
            SCI_info: [
                "非SCI",
                "SCI 一区",
                "SCI 二区",
                "SCI 三区",
                "SCI 四区",
            ],
            tableData_paper: [],
            tableData_patent: [],
            // src_photo: '@/assets/images/student/兰文尉.jpg'
        }
    },
    async created() {
        this.student = await csvQueryStudent.findStudentById(this.$route.query.stuid)
        this.tableData_paper = await csvQueryPaper.findPaperByAuthor(this.student.name, this.student.name_en)
        this.tableData_patent = await csvQueryPatent.findByAuthor(this.student.name)
        // this.getInfo();
        // this.getPaper();
        // this.getPatent();
    },
    methods: {
        getInfo() {
            let studentName = this.$route.query.name;
            console.log(studentName);
            if (studentName != null) {
                axios.get(`${process.env.VUE_APP_API_ENDPOINT}/student/findByName/${studentName}`).then((res) => {
                    console.log(res);
                    this.student = res.data
                    this.getPaper();
                    this.getPatent();
                }).catch((err) => {
                    console.log(err);
                })
                // console.log(111, studentName);
            } else {
                location.href = "/studentList";
            }
        },
        getPaper() {
            axios.get(`${process.env.VUE_APP_API_ENDPOINT}/paper/findByAuthor/${this.student.name}/${this.student.name_en}`).then((res) => {
                console.log(111, res);
                this.tableData_paper = res.data;
                this.changeBold(this.student.name_en);
                // console.log(this.tableData);  
            }).catch((err) => {
                console.log(err);
            })
            // 获取论文数据
        },
        getPatent() {
            axios.get(`${process.env.VUE_APP_API_ENDPOINT}/patent/findByAuthor/${this.student.name}`).then((res) => {
                console.log(111, res);
                this.tableData_patent = res.data;
                // this.changeBold(this.student.name_en);
                // console.log(this.tableData);  
            }).catch((err) => {
                console.log(err);
            })
        },
        changeBold(name) {
            for (let i = 0; i < this.tableData.length; i++) {
                console.log("name:" + name);
                this.tableData[i].introduction.replace(name, `<span style="font-weight: bold">${name}</span>`)
                console.log("change" + i.toString() + ":" + this.tableData[i].introduction);
            }
        },
        formatIntroduction(introduction) {
            const escapeRegExp = (string) => {
                return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
            };

            let formatted = introduction;
            const namesToBold = [
                this.student.name, 
                this.student.name_en
            ].filter(name => name); // 过滤空值

            namesToBold.forEach(name => {
                const escapedName = escapeRegExp(name);
                const regex = new RegExp(escapedName, 'g');
                formatted = formatted.replace(regex, `<strong style="font-weight: 900; color: #000000;">${name}</strong>`);
            });

            return formatted;
        },
        goBack() {
            location.href = "#/studentList"
        }
    }
}
</script>

