<template>
  <div>
    <!-- STRAT NAVBAR -->
    <nav class="navbar navbar-expand-lg fixed-top navbar-white navbar-custom sticky sticky-dark" id="navbar">
      <div class="container">
        <!-- LOGO -->
        <!-- <a class="navbar-brand logo text-uppercase" href="/">
          <i class="mdi mdi-alien"></i>Hiric
        </a> -->
        <div style="margin-left: -5%">
          <a href="/">
            <img src="../../assets/images/logo_home.jpg" :width="bannerHeight + 'px'">
          </a>
          <!-- <a href="/">
            <img src="../../assets/images/serc.jpg" width="240px" style="margin-left: 10px;">
          </a> -->
          <!-- <b style="margin-left: 50px; font-size: 24px;font-weight: 700;color: black; font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;">软件工程研究中心</b> -->
        </div>

        <!-- <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarCollapse"
          aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation" @click="toggleMenu()">
          <i class="mdi mdi-menu"></i>
        </button> -->
        <div class="collapse navbar-collapse" id="navbarCollapse">
          <ul class="navbar-nav navbar-center" id="mySidenav">
            <!-- <ul class="navbar-nav navbar-center" id="mySidenav" v-scroll-spy-active="{ class: 'active' }"> -->
            <li class="nav-item">
              <a class="nav-link" href="#/">主页</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#/newList">中心动态</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#/achievement">学术成果</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#/studentList">学生信息</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#/culture">中心建设</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#/tool">工具展示</a>
            </li>
          </ul>
          <!-- <div class="nav-button ml-auto">
            <ul class="nav navbar-nav navbar-right">
              <li>
                <button type="button" class="btn btn-custom navbar-btn btn-rounded">Try it Free</button>
              </li>
            </ul>
          </div> -->
        </div>
      </div>
    </nav>
    <!-- END NAVBAR -->

    <div style="margin-top: 100px;margin-left: 10%;margin-right: 10%;margin-bottom: 30px;">
      <div align="right">
        <el-page-header @back="goBack" content="导师信息" style="margin-bottom: 25px;">
        </el-page-header>
      </div>
      <hr>
      <el-row>
        <el-col :span="8">
          <div class="grid-content" align="center">
            <el-image style="width: 200px;" :src="require('@/assets/images/teacher/' + teacher.photo)" :fit="fit"></el-image>
          </div>
        </el-col>
        <el-col :span="16">
          <div class="grid-content" style="height: 200px;font-size: 18px;padding-top: 20px;">
            <el-row>
              <el-col :span="12">
                <span style="font-weight: bold;">姓名：</span>{{ teacher.name }}
              </el-col>
              <el-col :span="12">
                <span style="font-weight: bold;">职称：</span>{{ teacher.title }}
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <span style="font-weight: bold;">研究方向：</span>{{ teacher.direction }}
              </el-col>
              <el-col :span="12">
                <span style="font-weight: bold;">邮箱：</span>{{ teacher.email }}
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24">
                <span style="font-weight: bold;">个人介绍：</span>{{ teacher.introduction }}
              </el-col>
            </el-row>
          </div>
        </el-col>
      </el-row>
      <el-divider>发表论文 ({{ tableData_paper.length }}篇) </el-divider>
      <el-table :data="tableData_paper">
        <el-table-column type="index" min-width="80" header-align="center">
        </el-table-column>
        <el-table-column prop="introduction" label="描述" min-width="600" header-align="center">
        </el-table-column>
        <el-table-column prop="type" label="类型" min-width="110" align="center">
          <template slot-scope="scope">
            <el-tag :type="scope.row.type != 'Conference' ? 'success' : 'info'" disable-transitions>{{
              scope.row.type
            }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="year" label="年份" min-width="100" align="center">
        </el-table-column>
        <el-table-column prop="CCF" label="CCF分类" min-width="150" align="center">
          <template slot-scope="scope">
            <el-tag :type="scope.row.CCF != '0' ? 'primary' : 'danger'" disable-transitions>{{
              CCF_info[scope.row.CCF]
            }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="SCI" label="检索类型" min-width="150" align="center">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.SCI != '0'" :type="'primary'" disable-transitions>{{
              SCI_info[scope.row.SCI]
            }}</el-tag>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <el-tag v-if="scope.row.EI != '0'" :type="'warning'" disable-transitions>EI</el-tag>
            <el-tag v-if="scope.row.EI == '0' && scope.row.SCI == '0'" :type="'danger'" disable-transitions>其他</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="published" label="论文状态" min-width="100" align="center">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.published == 1" type="success" effect="plain">
              Published
            </el-tag>
            <el-tag v-else type="info" effect="plain">
              Accepted
            </el-tag>
          </template>
        </el-table-column>
      </el-table>
      <br>
      <el-divider>提交专利({{ tableData_patent.length }}项)</el-divider>
      <el-table :data="tableData_patent">
        <el-table-column prop="author" label="作者" min-width="350" align="center">
        </el-table-column>
        <el-table-column prop="title" label="专利名称" min-width="400" align="center">
        </el-table-column>
        <el-table-column prop="patentId" label="申请号" min-width="150" align="center">
        </el-table-column>
        <el-table-column prop="authorized" label="状态" min-width="100" align="center">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.authorized != '0'" type="primary" disable-transitions>已授权</el-tag>
            <el-tag v-if="scope.row.authorized == '0'" type="warning" disable-transitions>未授权</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <Footer />
    <Switcher />
  </div>
</template>

<script>
import Footer from "@/components/footer";
import Switcher from "@/components/switcher";
import axios from 'axios';
import * as csvQueryTeacher from '../../assets/js/teacher.js';
import * as csvQueryPaper from '../../assets/js/paper.js';
import * as csvQueryPatent from '../../assets/js/patent.js';

/**
 * Index-1 component
 */
export default {
  name: "teacherInfo",
  data() {
    return {
      teacher: {},
      api_url: process.env.VUE_APP_API_ENDPOINT,
      tableData_paper: [],
      tableData_patent: [],
      CCF_info: [
        "其他",
        "T1、A类中文期刊",
        "A类国际期刊",
        "B类国际期刊",
        "C类国际期刊",
        "A类国际会议",
        "B类国际会议",
        "C类国际会议"
      ],
      SCI_info: [
        "非SCI",
        "SCI 一区",
        "SCI 二区",
        "SCI 三区",
        "SCI 四区",
      ],
      bannerHeight: 0
    }
  },
  mounted() {
    // 首次加载时,初始化高度
    this.screenWidth = window.innerWidth
    if(this.screenWidth > 1000){
        this.bannerHeight = 500
      }else{
        this.bannerHeight = 0.9 * this.screenWidth
      }
    // 窗口大小发生改变
    window.onresize = () => {
      this.screenWidth = window.innerWidth
      if(this.screenWidth > 1000){
        this.bannerHeight = 500
      }else{
        this.bannerHeight = 0.9 * this.screenWidth
      }
    }
  },
  components: {
    Footer,
    Switcher
  },
  async created() {
    this.teacher = await csvQueryTeacher.findByName(this.$route.query.name)
    this.tableData_paper = await csvQueryPaper.findPaperByAuthor(this.teacher.name, this.teacher.name_en)
    this.tableData_patent = await csvQueryPatent.findByAuthor(this.teacher.name)
    // this.getInfo()
  },
  methods: {
    /**
     * Toggle menu
     */
    toggleMenu() {
      document.getElementById("navbarCollapse").classList.toggle("show");
    },
    getInfo() {
      let teachertName = this.$route.query.name;
      console.log(111, teachertName);
      if (teachertName != null) {
        axios.get(`${process.env.VUE_APP_API_ENDPOINT}/teacher/findByName/${teachertName}`).then((res) => {
          console.log(res);
          this.teacher = res.data
          this.getPaper();
          this.getPatent();
        }).catch((err) => {
          console.log(err);
        })
      } else {
        // location.href = "/studentList";
      }
    },
    getPaper() {
      axios.get(`${process.env.VUE_APP_API_ENDPOINT}/paper/findByAuthor/${this.teacher.name}/${this.teacher.name_en}`).then((res) => {
        console.log(222, res.data);
        this.tableData_paper = res.data;
        console.log(222, this.tableData_paper);
      }).catch((err) => {
        console.log(err);
      })
    },
    getPatent() {
      axios.get(`${process.env.VUE_APP_API_ENDPOINT}/patent/findByAuthor/${this.teacher.name}`).then((res) => {
        console.log(111, res);
        this.tableData_patent = res.data;
      }).catch((err) => {
        console.log(err);
      })
    },
    goBack() {
      location.href = "#/"
    }
  },
};
</script>

