<template>
  <!--START ABOUT-US-->
  <section class="section" id="achievement_paper">
    <div>
      <div style="width: 100%;" id="paper">
        <el-form ref="form" :model="sizeForm" label-width="80px" size="mini">
          <el-form-item label="论文类型" style="margin-bottom: 5px;">
            <el-checkbox-group v-model="sizeForm.type" @change="findByConditions">
              <el-checkbox-button label="Journal" name="type">期刊</el-checkbox-button>
              <el-checkbox-button label="Conference" name="type">会议</el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="论文状态" style="margin-bottom: 5px;">
            <el-checkbox-group v-model="sizeForm.published" @change="findByConditions">
              <el-checkbox-button label="1" name="published">Published</el-checkbox-button>
              <el-checkbox-button label="0" name="published">Accepted</el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="CCF分类" style="margin-bottom: 5px;">
            <el-checkbox-group v-model="sizeForm.CCF" @change="findByConditions">
              <el-checkbox-button label="1" name="CCF">T1、A类中文期刊</el-checkbox-button>
              <el-checkbox-button label="2" name="CCF">A类国际期刊</el-checkbox-button>
              <el-checkbox-button label="3" name="CCF">B类国际期刊</el-checkbox-button>
              <el-checkbox-button label="4" name="CCF">C类国际期刊</el-checkbox-button>
              <el-checkbox-button label="5" name="CCF">A类国际会议</el-checkbox-button>
              <el-checkbox-button label="6" name="CCF">B类国际会议</el-checkbox-button>
              <el-checkbox-button label="7" name="CCF">C类国际会议</el-checkbox-button>
              <el-checkbox-button label="0" name="CCF">其他</el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="检索类型" style="margin-bottom: 5px;">
            <el-checkbox-group v-model="sizeForm.SCI" @change="findByConditions">
              <el-checkbox-button label="1" name="SCI">SCI 一区</el-checkbox-button>
              <el-checkbox-button label="2" name="SCI">SCI 二区</el-checkbox-button>
              <el-checkbox-button label="3" name="SCI">SCI 三区</el-checkbox-button>
              <el-checkbox-button label="4" name="SCI">SCI 四区</el-checkbox-button>
              <el-checkbox-button label="EI" name="SCI">EI</el-checkbox-button>
              <el-checkbox-button label="0" name="SCI">其他</el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
        </el-form>
        <el-divider content-position="center">共 {{ tableData.length }} 条记录</el-divider>
        <el-table :data="tableData">
          <el-table-column type="index" min-width="50">
          </el-table-column>
          <el-table-column prop="introduction" label="描述" min-width="600" header-align="center">
          </el-table-column>
          <el-table-column prop="type" label="类型" min-width="110" align="center">
            <template slot-scope="scope">
              <el-tag :type="scope.row.type != 'Conference' ? 'success' : 'danger'" disable-transitions>{{ scope.row.type
                }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="year" label="年份" min-width="100" align="center">
          </el-table-column>
          <el-table-column prop="CCF" label="CCF分类" min-width="150" align="center">
            <template slot-scope="scope">
              <el-tag :type="scope.row.CCF != 0 ? 'primary' : 'info'" disable-transitions>{{ CCF_info[scope.row.CCF]
                }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="SCI" label="检索类型" min-width="150" align="center">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.SCI != 0" :type="'primary'" disable-transitions>{{ SCI_info[scope.row.SCI]
                }}</el-tag>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <el-tag v-if="scope.row.EI != 0" :type="'warning'" disable-transitions>EI</el-tag>
              <el-tag v-if="scope.row.EI == 0 && scope.row.SCI == 0" :type="'info'" disable-transitions>其他</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="published" label="论文状态" min-width="100" align="center">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.published == 1" type="success" effect="plain">
                Published
              </el-tag>
              <el-tag v-else type="info" effect="plain">
                Accepted
              </el-tag>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </section>
  <!--END ABOUT-US-->
</template>

<script>
/**
 * About component
 */
import axios from "axios";
import * as csvQueryPaper from '../assets/js/paper.js';

export default {
  name: 'AchievementPaper',
  data() {
    return {
      sizeForm: {
        SCI: [],
        type: [],
        CCF: [],
        published: []
      },
      CCF_info: [
        "其他",
        "T1、A类中文期刊",
        "A类国际期刊",
        "B类国际期刊",
        "C类国际期刊",
        "A类国际会议",
        "B类国际会议",
        "C类国际会议"
      ],
      SCI_info: [
        "非SCI",
        "SCI 一区",
        "SCI 二区",
        "SCI 三区",
        "SCI 四区",
      ],
      tableData: [],
    }
  },
  async created() {
    this.tableData = await csvQueryPaper.findAll();
    // this.getData()
  },
  methods: {
    getData() {
      axios.get(`${process.env.VUE_APP_API_ENDPOINT}/paper/findAll`).then((res) => {
        this.tableData = res.data
      }).catch((err) => {
        console.log(err);
      })
    },
    async findByConditions() {
      console.log(this.sizeForm);
      this.tableData = await csvQueryPaper.findByConditionCsv(this.sizeForm);
      // axios.post(`${process.env.VUE_APP_API_ENDPOINT}/paper/findByConditions`, this.sizeForm).then((res) => {
      //   console.log(res);
      //   this.tableData = res.data
      // }).catch((err) => {
      //   console.log(err);
      // })
    }
  }
}
</script>

<style>
.section {
  margin-top: -60px;
}
</style>
