// csv query for teacher

import * as d3 from 'd3';

// 查询导师列表
export async function findAll() {
    const decoder = new TextDecoder('utf-8');
    let response = await fetch('/data/teachers.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql("SELECT * FROM ? WHERE isDel = '0'", [parsedData]);
    
    for (let i = 0; i < result.length; i++) {
        const element = result[i];
        
    }
    console.log(123, result);
    
    return result;
}

// 根据姓名查询导师信息
export async function findByName(name) {
    const decoder = new TextDecoder('utf-8');
    let response = await fetch('/data/teachers.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据
    
    const result = alasql(`SELECT * FROM ? WHERE isDel='0' and name='${name}'`, [parsedData]);

    return result[0];
}