// json query for tools

export async function findFuzzers() {
    let response = await fetch('/data/tools.json');
    
    let data = await response.json();
    console.log(1212, data);

    return data["fuzzers"];
}

export async function findVulDetector() {
    let response = await fetch('/data/tools.json');
    
    let data = await response.json();
    console.log(1212, data);

    return data["vulDetector"];
}
