<template>
  <!--START ABOUT-US-->
  <section class="section" id="achievement_patent">
    <div class="container">
      <div style="width: 100%;" id="paper">
        <el-form ref="form" :model="sizeForm" label-width="120px">
          <el-form-item label="状态：">
            <el-radio-group v-model="sizeForm.authorized" size="small" @change="updateData">
              <el-radio border label="全部"></el-radio>
              <el-radio border label="已授权"></el-radio>
              <el-radio border label="未授权"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <el-divider content-position="center">共 {{ tableData.length }} 条记录</el-divider>
        <el-table :data="tableData">
          <el-table-column type="index" min-width="80" align="center">
          </el-table-column>
          <el-table-column prop="author" label="作者" min-width="350" align="center">
          </el-table-column>
          <el-table-column prop="title" label="专利名称" min-width="400" align="center">
          </el-table-column>
          <el-table-column prop="patentId" label="申请号" min-width="150" align="center">
          </el-table-column>
          <el-table-column prop="authorized" label="状态" min-width="100" align="center">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.authorized != 0" type="primary" disable-transitions>已授权</el-tag>
              <el-tag v-if="scope.row.authorized == 0" type="warning" disable-transitions>未授权</el-tag>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </section>
  <!--END ABOUT-US-->
</template>

<script>
/**
 * About component
 */
import axios from "axios";
import * as csvQueryPatent from '../assets/js/patent.js';

export default {
  name: 'AchievementPatent',
  data() {
    return {
      tableData: [
        // {
        //   author: '兰文尉, 黄晨, 于婷婷, 崔展齐',
        //   title: '一种测试用例生成及模糊测试方法、系统及可存储介质',
        //   patentId: '2024100997165',
        //   authorized: 0,
        // },
        // {
        //   author: '杨慧文, 崔展齐, 贾明华, 刘秀磊, 刘建宾, 郑丽伟',
        //   title: '面向Solidity智能合约的缺陷预测方法',
        //   patentId: '2020115620731',
        //   authorized: 1,
        // },
      ],
      sizeForm: {
        authorized: '全部',
      }
    }
  },
  async created() {
    this.tableData = await csvQueryPatent.findAll()
    // this.getData()
  },
  methods: {
    getData() {
      axios.get(`${process.env.VUE_APP_API_ENDPOINT}/patent/findAll`).then((res) => {
        console.log(res);
        this.tableData = res.data
        console.log('000');
      }).catch((err) => {
        console.log(err);
      })
    },
    getDataByAuthorized(authorized){
      axios.get(`${process.env.VUE_APP_API_ENDPOINT}/patent/findByAuthorized/${authorized}`).then((res) => {
            this.tableData = res.data
          }).catch((err) => {
            console.log(err);
          })
    },
    async updateData() {
      if (this.sizeForm.authorized == '全部') {
        this.tableData = await csvQueryPatent.findAll()
      } else {
        if (this.sizeForm.authorized == '已授权') {
          this.tableData = await csvQueryPatent.findByAuthorized(1)
        }else{
          this.tableData = await csvQueryPatent.findByAuthorized(0)
        }
      }
    }
  }
}
</script>

<style>
.section {
  margin-top: -60px;
}
</style>
