// csv query for patents

import * as d3 from 'd3';

// 查询已授权、未授权数量
export async function findAuthorizedCount() {
    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/patents.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql("SELECT authorized, COUNT(*) FROM ? WHERE isDel = '0' GROUP BY authorized", [parsedData]);

    let type_json = {
        "authorized": 0,
        "unauthorized": 0
    }
    for (let item in result) {
        let re = result[item]
        if (re["authorized"] == "1") {
            type_json["authorized"] = re["COUNT(*)"]
        } else {
            type_json["unauthorized"] = re["COUNT(*)"]
        }
    }
    return type_json;
}

// 查询所有专利
export async function findAll() {
    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/patents.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql("SELECT * FROM ? WHERE isDel = '0' order by patentId desc", [parsedData]);

    return result;
}

// 根据授权情况查询专利列表
export async function findByAuthorized(authorized) {
    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/patents.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql(`SELECT * FROM ? WHERE isDel = '0' and authorized='${authorized}' order by patentId desc`, [parsedData]);

    console.log(123, result);
    
    return result;
}

// 根据作者查询专利列表
export async function findByAuthor(author) {
    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/patents.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql(`SELECT * FROM ? WHERE isDel = '0' and author like'%${author}%' order by patentId desc`, [parsedData]);

    return result;
}