        <el-divider>
          <h4 style="color: grey;">论文分类</h4>
        </el-divider>
        <el-row>
          <el-col :span="24">
            <div class="grid-content" style="margin: 5px;">
              <div id="main_bar_1" style="width: 100%; height: 500px;">

              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <br />
