<script>
/**
 * ResearchShow component
 */

import * as echarts from 'echarts';
import axios from "axios";
import * as csvQueryPaper from '../assets/js/paper.js';
import * as csvQueryPatent from '../assets/js/patent.js';
import * as csvQueryCopyright from '../assets/js/copyright.js';

export default {
  name: 'ResearchShow',
  data() {
    return {
      // paper: [中文a刊，英文a刊，英文b刊，英文c刊，英文a会，英文b会，英文c会，其他]
      paper: [],
      type_count: {
        conference: 0,
        journal: 0
      },
      patent: {
        authorized: 0,
        unauthorized: 0
      },
      copyright_num: 0,
    }
  },
  async created() {
    // this.getData()
    this.paper = await csvQueryPaper.findYearAndCCF();
    this.type_count = await csvQueryPaper.findTypeCount();
    this.patent = await csvQueryPatent.findAuthorizedCount();
    this.copyright_num = await csvQueryCopyright.findCopyrightCount();
    this.draw_bar_1()
    this.draw_pie_1()
    this.draw_pie_2()
    this.draw_pie_3()
  },
  mounted() {
    this.draw_bar_1()
    this.draw_pie_1()
    this.draw_pie_2()
    this.draw_pie_3()

  },
  methods: {
    draw_bar_1() {
      let paper = []
      paper = this.paper;
      let year = ['<=2016', '2017', '2018', '2019', '2020', '2021', '2022', '2023', '2024', '2025']
      let source = ['CCF A 中文期刊', 'CCF A 英文期刊', 'CCF B 英文期刊', 'CCF C 英文期刊', 'CCF A 英文会议', 'CCF B 英文会议', 'CCF C 英文会议', '其他']
      var chartDom = document.getElementById("main_bar_1");

      var myChart = echarts.init(chartDom);
      var option;

      option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // Use axis to trigger tooltip
            type: 'shadow' // 'shadow' as default; can also be 'line' or 'shadow'
          }
        },
        legend: {},
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',  // 将 xAxis 设为 'category'
          data: year
        },
        yAxis: {
          type: 'value'  // 将 yAxis 设为 'value'
        },
        series: [
          {
            name: source[0],
            type: 'bar',
            stack: 'total',
            label: {
              show: false
            },
            emphasis: {
              focus: 'series'
            },
            data: paper[0]
          },
          {
            name: source[1],
            type: 'bar',
            stack: 'total',
            label: {
              show: false
            },
            emphasis: {
              focus: 'series'
            },
            data: paper[1]
          },
          {
            name: source[2],
            type: 'bar',
            stack: 'total',
            label: {
              show: false
            },
            emphasis: {
              focus: 'series'
            },
            data: paper[2]
          },
          {
            name: source[3],
            type: 'bar',
            stack: 'total',
            label: {
              show: false
            },
            emphasis: {
              focus: 'series'
            },
            data: paper[3]
          },
          {
            name: source[4],
            type: 'bar',
            stack: 'total',
            label: {
              show: false
            },
            emphasis: {
              focus: 'series'
            },
            data: paper[4]
          },
          {
            name: source[5],
            type: 'bar',
            stack: 'total',
            label: {
              show: false
            },
            emphasis: {
              focus: 'series'
            },
            data: paper[5]
          },
          {
            name: source[6],
            type: 'bar',
            stack: 'total',
            label: {
              show: false
            },
            emphasis: {
              focus: 'series'
            },
            data: paper[6]
          },
          {
            name: source[7],
            type: 'bar',
            stack: 'total',
            label: {
              show: false
            },
            emphasis: {
              focus: 'series'
            },
            data: paper[7]
          },
        ]
      };
      option && myChart.setOption(option);
    },
    draw_pie_1() {
      var chartDom = document.getElementById('main_pie_1');
      var myChart = echarts.init(chartDom);
      var option;

      option = {
        tooltip: {
          trigger: 'item'
        },
        legend: {
          top: '5%',
          left: 'center'
        },
        series: [
          {
            name: '发表论文',
            type: 'pie',
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 0,
              borderColor: '#fff',
              borderWidth: 0
            },
            label: {
              show: false,
              position: 'center',
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 15,
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: false
            },
            data: [
              { value: this.type_count.Journal, name: '期刊' },
              { value: this.type_count.Conference, name: '会议' },
            ]
          }
        ]
      };

      option && myChart.setOption(option);
    },
    draw_pie_2() {
      var chartDom = document.getElementById('main_pie_2');
      var myChart = echarts.init(chartDom);
      var option;

      option = {
        tooltip: {
          trigger: 'item'
        },
        legend: {
          top: '5%',
          left: 'center'
        },
        series: [
          {
            name: '提交专利',
            type: 'pie',
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 0,
              borderColor: '#fff',
              borderWidth: 0
            },
            label: {
              show: false,
              position: 'center',
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 15,
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: false
            },
            data: [
              { value: this.patent.authorized, name: '已授权' },
              { value: this.patent.unauthorized, name: '未授权' },
            ]
          }
        ]
      };

      option && myChart.setOption(option);
    },
    draw_pie_3() {
      var chartDom = document.getElementById('main_pie_3');
      var myChart = echarts.init(chartDom);
      var option;

      option = {
        tooltip: {
          trigger: 'item'
        },
        legend: {
          top: '5%',
          left: 'center'
        },
        series: [
          {
            name: '申请软著',
            type: 'pie',
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 0,
              borderColor: '#fff',
              borderWidth: 0
            },
            label: {
              show: false,
              position: 'center',
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 15,
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: false
            },
            data: [
              { value: this.copyright_num, name: '软著' },
            ]
          }
        ]
      };

      option && myChart.setOption(option);
    },
    getData() {
      // this.draw_bar_1();
      axios.get(`${process.env.VUE_APP_API_ENDPOINT}/findResearchShow`).then((res) => {
        console.log(res);
        // this.paper = res.data.year_CCF_list
        // this.draw_bar_1();
        // this.type_count = res.data.type_count
        // this.draw_pie_1();
        // this.patent = res.data.patent
        // this.draw_pie_2();
        // this.copyright_num = res.data.copyright_num
        // this.draw_pie_3();
      }).catch((err) => {
        console.log(err);
      })
    }
  }
}
</script>