// csv query for copyright

import * as d3 from 'd3';

export async function findCopyrightCount() {
    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/copyrights.csv');
    // let response = await fetch('/data/copyrights.xlsx');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql("SELECT * FROM ? WHERE isDel = '0'", [parsedData]);
    
    return result.length;
}

export async function findAll() {
    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/copyrights.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql("SELECT * FROM ? WHERE isDel='0' order by registrationNum desc", [parsedData]);
    
    return result;
}