// 根据学位查询学生列表
export async function findAllStudentsByDegree() {
    const decoder = new TextDecoder('utf-8');
    let response = await fetch('/data/students.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);
    let parsedData = d3.csvParse(decodedData);

    // 过滤出有效学生数据（isDel = '0'）
    const validData = parsedData.filter(d => d.isDel === '0');

    // 分为硕士和博士两个列表
    const masterRaw = validData.filter(d => d.degree === '0');
    const phdRaw = validData.filter(d => d.degree === '1');

    // 工具函数：按 year 分组
    function groupByYear(data) {
        const yearMap = {};
        data.forEach(d => {
            if (!yearMap[d.year]) {
                yearMap[d.year] = [];
            }
            yearMap[d.year].push({ name: d.name, id: d.id });
        });

        // 返回格式为 [{ year: ..., students: [...] }]
        return Object.entries(yearMap)
            .sort((a, b) => b[0] - a[0]) // 年份降序
            .map(([year, students]) => ({
                year,
                students
            }));
    }

    return {
        masterList: groupByYear(masterRaw),
        phdList: groupByYear(phdRaw)
    };
}
