 */
export default {
  components: {
    Footer,
    Switcher,
    Student_info
  },
  data() {
    return {
      bannerHeight: 0
    };
  },
  mounted() {
    // 首次加载时,初始化高度
    this.screenWidth = window.innerWidth
    if(this.screenWidth > 1000){
        this.bannerHeight = 500
      }else{
        this.bannerHeight = 0.9 * this.screenWidth
      }
    // 窗口大小发生改变
    window.onresize = () => {
      this.screenWidth = window.innerWidth
      if(this.screenWidth > 1000){
        this.bannerHeight = 500
      }else{
        this.bannerHeight = 0.9 * this.screenWidth
      }
