<template>
    <!--START ABOUT-US-->
    <section class="section" id="student_info">
        <div>
            <div style="width: 100%;" id="student">
                <div>
                    <el-page-header @back="goBack" content="动态详情" style="margin-bottom: 25px;">
                    </el-page-header>
                </div>
                <hr>
                <div>
                    <h3 align="center">{{ this.new.title }}</h3>
                    <h4 align="center" style="margin: 20px 0px 10px 0px; color: gray;font-weight: lighter;">{{
                        this.new.date }}</h4>
                    <h4 v-if="this.new.doi" style="margin: 20px 0px 40px 0px;font-weight: lighter;">
                        <span style="font-weight: bold;">DOI: </span><a style="color: black;" :href="this.new.href" target="_blank">{{ this.new.doi }}</a>
                    </h4>
                    <div v-for="(item, i) in this.new.content" :key="i">
                        <p v-if="item['types'] == 'text'" style="font-size: 20px;line-height: 1.5;">
                            {{ item.info }}
                        </p>
                        <div v-if="item['types'] == 'image'" align="center" style="width: 60%;margin: auto">
                            <el-image width="100" :src="require('@/assets/images/new/' + item.info)"
                                :preview-src-list="[require('@/assets/images/new/' + item.info)]">
                            </el-image>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    <!--END ABOUT-US-->
</template>

<script>
/**
 * About component
 */
import * as jsonQueryNew from '@/assets/js/new.js';

export default {
    name: 'NewInfo',
    data() {
        return {
            new: {}
        }
    },
    async created() {
        this.new = await jsonQueryNew.findById(this.$route.query.newId)
        // console.log(this.new.content[1]["types"]);
    },
    methods: {
        goBack() {
            location.href = "#/newList"
        }
    }
}
</script>

<style>
.section {
    margin-top: -60px;
}

.time {
    font-size: 13px;
    color: #999;
}

.bottom {
    margin-top: 13px;
    line-height: 12px;
}

.button {
    padding: 0;
    float: right;
}

.image {
    width: 100%;
    display: block;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both
}

.student_name {
    font-size: 20px;
    color: black;
    margin-right: 10px;
}

p {
    text-indent: 2em;
    /* 缩进2个字符的宽度 */
    font-size: 16px;
}
</style>