<template>
  <!--START ABOUT-US-->
  <section class="section" id="ShowPicture" style="margin-top: 80px;padding: 0px;">
    <div :style="'width: 100%;height: '+bannerHeight + 'px'" align="center">
      <el-carousel type="card" indicator-position="none" :style="'height: ' + bannerHeight + 'px'"
        interval="5000" arrow="none">
        <!-- <el-carousel-item v-for="item in 3" :key="item" :style="'height: ' + height"> 
          <img src="@/assets/images/showPicture/pic1.jpg" width="100%"/>
        </el-carousel-item> -->
        <el-carousel-item :style="'height: ' + bannerHeight + 'px'">
          <img src="@/assets/images/showPicture/pic4.jpg" width="100%" height="100%"/>
        </el-carousel-item>
        <el-carousel-item :style="'height: ' + bannerHeight + 'px'">
          <img src="@/assets/images/showPicture/pic0.jpg" width="100%" height="100%"/>
        </el-carousel-item>
        <el-carousel-item :style="'height: ' + bannerHeight + 'px'">
          <img src="@/assets/images/showPicture/pic1.jpg" width="100%" height="100%"/>
        </el-carousel-item>
        <el-carousel-item :style="'height: ' + bannerHeight + 'px'">
          <img src="@/assets/images/showPicture/pic2.jpg" width="100%" height="100%"/>
        </el-carousel-item>
      </el-carousel>
    </div>
  </section>
  <!--END ABOUT-US-->
</template>

<script>
/**
 * About component
 */
export default {
  name: 'ShowPicture',
  data() {
    return {
      height: '100%',
      bannerHeight: 0
    }
  },
  mounted() {
    // 首次加载时,初始化高度
    this.screenWidth = window.innerWidth
    this.bannerHeight = 0.3 * this.screenWidth
    // 窗口大小发生改变
    window.onresize = () => {
      this.screenWidth = window.innerWidth
      this.bannerHeight = 0.3 * this.screenWidth
    }
  }

}
</script>

<style>
.el-carousel__item h3 {
  color: #475669;
  font-size: 18px;
  opacity: 0.75;
  line-height: 300px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}
</style>