// 查询期刊和会议论文数量
export async function findTypeCount() {
    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/papers.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql("SELECT type, COUNT(*) FROM ? WHERE isDel = '0' GROUP BY type", [parsedData]);

    let type_json = {
        "Journal": 0,
        "Conference": 0
    }
    for (let item in result) {
        let re = result[item]
        if (re["type"] == "Journal") {
            type_json["Journal"] = re["COUNT(*)"]
        } else {
            type_json["Conference"] = re["COUNT(*)"]
        }
    }
    return type_json;
}

// 查询所有论文
export async function findAll() {
    console.log("begin to findAll papers");
    
    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/papers.csv');
    // let response = require('@/assets/data/papers.csv').defult;
    console.log(12121, response);
    
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql("SELECT * FROM ? WHERE isDel = '0' order by year desc, id desc", [parsedData]);

    return result;
}

// 根据复选框条件查询论文列表
export async function findByConditionCsv(condition_json) {
    let conditions_list = ["isDel='0'"]
    let CCF_list = condition_json['CCF']
    let SCI_list = condition_json['SCI']
    let type_list = condition_json['type']
    let published_list = condition_json['published']
    let EI_list = []
    let temp;
    let flag = 0;

    if (SCI_list.includes('EI')) {
        EI_list.push('1')
        let indexToRemove = SCI_list.indexOf('EI'); // 找到EI的索引
        console.log(1210, indexToRemove);

        if (indexToRemove != -1) {
            flag = 1;
            SCI_list.splice(indexToRemove, 1); // 删除索引为 indexToRemove 的元素
        }
    }
    if (CCF_list.length > 0) {
        temp = CCF_list.join("','")
        conditions_list.push(`CCF in ('${temp}')`)
    }
    if (SCI_list.length > 0) {
        temp = SCI_list.join("','")
        conditions_list.push(`SCI in ('${temp}')`)
    }
    if (published_list.length > 0) {
        temp = published_list.join("','")
        conditions_list.push(`published in ('${temp}')`)
    }
    if (type_list.length > 0) {
        if (type_list.length == 2) conditions_list.push(`type in ("${type_list[0]}", "${type_list[1]}")`)
        if (type_list.length == 1) conditions_list.push(`type in ("${type_list[0]}")`)
    }
    if (SCI_list.includes('0')) {
        EI_list.push('0')
        temp = EI_list.join("','")
        conditions_list.push(`EI in ('${temp}')`)
    } else{
        if (EI_list.includes("1")) {
            temp = EI_list.join("','")
            conditions_list.push(`EI in ('${temp}')`)
        }
    }
    const conditions = conditions_list.join(" and ")
    const sql = `select * from ? where ${conditions} order by year desc, id desc`

    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/papers.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql(sql, [parsedData]);

    if (flag == 1) SCI_list.push('EI')

    return result;
}


// 根据作者查询论文列表
export async function findPaperByAuthor(name, name_en) {
    const decoder = new TextDecoder('gbk');
    let response = await fetch('/data/papers.csv');
    let data = await response.arrayBuffer();
    let decodedData = decoder.decode(data);  // 将二进制数据解码为字符串
    let parsedData = d3.csvParse(decodedData);  // 使用D3解析CSV数据

    const result = alasql(`SELECT * FROM ? WHERE isDel='0' and author like "%${name}%" or author like "%${name_en}%" order by year desc, id desc`, [parsedData]);

    return result;
}
